import React, {Component} from 'react'
import {Link} from "react-router-dom";
import {
    Row, Col, Table, Icon,
    Divider, Modal, Button,
    Drawer, Form, Input, Select, message
} from 'antd';
import {callapi, postapi} from "../helper";
import moment from "moment";
import config from "../config";
const FormItem = Form.Item;
const Option = Select.Option;

class CategoriesMetaWrapper extends Component {
    constructor(props) {
        super(props)
        this.state = {
            addCategorymeta: false,
            cat_meta_name: '',
            selectedmetavalue: [],
            cat_id: '',
            selectedcategory: '',

            selectedmetatype: '',
            categorymeta: [],
            categories: []
        }
    }

    componentWillMount() {
        this.getmeta()
        this.getCategories()
    }

    getmeta() {
        //getcategories
        const _this = this
        _this.setState({
            loading: true
        })
        const categories = callapi('getcategorymeta')
        categories.then((data) => {
            _this.setState({
                categorymeta: data
            })
        })
    }

    getCategories() {
        //getcategories
        const _this = this
        _this.setState({
            loading: true
        })
        const categories = callapi('getcategories')
        categories.then((data) => {
            _this.setState({
                categories: data
            })
        })
    }

    showmetaDrawer = () => {
        this.setState({
            addCategorymeta: true,
        });
    };

    onmetaClose = () => {
        this.setState({
            addCategorymeta: false,
            selectedcategory: '',
            cat_meta_name: '',
            selectedmetavalue: [],
            selectedmetatype: '',
        });

    };

    handleChange(name, event) {
        this.setState({
            [name]: event.target.value
        })
    }

    handleChangecategory = (value) => {
        this.setState({ selectedcategory: value })
    }

    handleChangemetatype = (value) => {
        this.setState({ selectedmetatype: value })
    }

    handleChangemetavalue = (value) => {
        this.setState({ selectedmetavalue: value })
    }

    savemeta() {
        const _this = this;
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                const addcat = postapi('addcategorymeta', {
                    cat_id: this.state.selectedcategory,
                    meta_name: this.state.cat_meta_name,
                    meta_type: this.state.selectedmetatype,
                    meta_value: this.state.selectedmetavalue,
                    added_at: moment().toISOString(),
                })

                addcat.then((data) => {
                    if (data) {
                        if (data.status === 'error') {
                            message.error(data.msg)
                            _this.setState({
                                loading: false
                            })
                        }
                        else {
                            _this.setState({
                                addMeta: false,
                                loading: false,
                            })
                            message.success(data.msg)
                            _this.getmeta()
                            _this.props.form.resetFields()
                        }
                    }
                    else {
                        message.warning("API error");
                    }

                })

            }
        });
    }

    deletecategorymeta(metaid) {
        const _this = this;
        const deleteplan = postapi('deletecategorymeta', {
            meta_id: metaid,
        })

        deleteplan.then((data) => {
            if (data.status === 'error') {
                message.error(data.msg)
                _this.setState({
                    loading: false
                })
            }
            else {
                _this.setState({
                    addMeta: false,
                    loading: false,
                })
                message.success(data.msg)
                _this.getmeta()
                _this.props.form.resetFields()
            }
        })
    }

    render() {
        let _this = this
        const {getFieldDecorator} = this.props.form;
        const columns = [
            {
                title: 'Meta name',
                dataIndex: 'meta_name',
                key: 'meta_name',
            },
            {
                title: 'Category',
                dataIndex: 'category',
                key: 'category',
            },
            {
                title: 'Meta type',
                dataIndex: 'meta_type',
                key: 'meta_type',
            },
            {
                title: 'Meta value',
                dataIndex: 'meta_value',
                key: 'meta_value',
            },
            {
                title: 'Action',
                key: 'action',
                render: (text, record) => {
                    return (
                        <span>
                        <Divider type="vertical"/>

                        <Button onClick={() => {
                            _this.deletecategorymeta(record.meta_id)

                        }} size='small' type="danger">
                            <Icon type="delete"/>
                        </Button>
                    </span>
                    )
                },
            }
        ];
        return (
            <div>

                <Row>
                    <Col lg={24}>
                        <div className='helper-bar'>
                            <Button type="primary" onClick={this.showmetaDrawer}>Add Category Meta</Button>
                            <Link to='/categories' className='ant-btn ant-btn-primary'>back</Link>
                        </div>

                        <Drawer
                            title="Add new category Meta"
                            width={520}
                            className="add_new_category_meta"
                            closable={false}
                            onClose={this.onmetaClose}
                            visible={this.state.addCategorymeta}
                        >
                            <div>
                                <Form>

                                    <FormItem
                                        label="Category Meta Name"
                                    >
                                        {getFieldDecorator('cat_meta_name', {
                                            rules: [
                                                {
                                                    required: true, message: 'You must type category meta name!',
                                                }
                                            ],
                                            initialValue: this.state.cat_meta_name
                                        })(
                                            <Input
                                                onChange={(e) => this.handleChange('cat_meta_name', e)}
                                                type='text'/>
                                        )}
                                    </FormItem>

                                    <Select
                                        size='large'
                                        className='form-control'
                                        placeholder="Select Category"
                                        onChange={this.handleChangecategory}
                                        value={this.state.selectedcategory}
                                    >
                                        <Option key='' value=''>Select Category</Option>
                                        {
                                            this.state.categories.map((item, index) => {
                                                return <Option key={index} value={item.cat_id}>{item.cat_name_en}</Option>
                                            })
                                        }
                                    </Select>

                                    <Select
                                        size='large'
                                        className='form-control'
                                        placeholder="Select meta type"
                                        onChange={this.handleChangemetatype}
                                        value={this.state.selectedmetatype}
                                    >
                                        <Option key='' value=''>Select Meta Type</Option>
                                        <Option value='text'>Text</Option>
                                        <Option value='option'>Option</Option>
                                    </Select>

                                    {
                                        (this.state.selectedmetatype === 'option') &&
                                            <Select
                                                size='large'
                                                mode='tags'
                                                className='form-control'
                                                placeholder="Add meta values"
                                                onChange={this.handleChangemetavalue}
                                                value={this.state.selectedmetavalue}
                                            />

                                    }

                                    <FormItem>
                                        <Button
                                            onClick={() => {
                                                this.savemeta()

                                            }}
                                            type="primary" htmlType="submit">Save category Meta</Button>
                                    </FormItem>
                                </Form>
                            </div>
                        </Drawer>

                    </Col>
                </Row>
                <Row>
                    <div style={{backgroundColor: '#fff', padding: 20}}>
                        <Table columns={columns} dataSource={this.state.categorymeta}/>
                    </div>

                </Row>
            </div>
        )
    }

}

const CategoriesMeta = Form.create()(CategoriesMetaWrapper);

export default CategoriesMeta