import React, {Component} from 'react'
import {Row, Col, Carousel, Spin, Icon, Layout, Button} from 'antd';
import ReactHtmlParser, {processNodes, convertNodeToElement, htmlparser2} from 'react-html-parser'
import moment from "moment";
import CommonHead from "./CommonHead";
import {postapi} from "../helper";
import config from "../config";
import FooterLinks from "./FooterLinks";
import BottomFooter from "./BottomFooter";
import SocialShare from "./SocialShare";
import RelatedProducts from "./RelatedProducts";
const {Content} = Layout;

class Details extends Component {
    constructor(props) {
        super(props)
        this.state = {
            single: [],
            loading: false,
            revalNumber: true,
        }
    }

    componentWillMount() {
        const _this = this
        _this.setState({
            loading: true
        })
        const categories = postapi('getsingleproduct', {
            pid: _this.props.match.params.id
        })
        categories.then((data) => {
            _this.setState({
                single: data.data,
                loading: false
            })
        })
    }

    revalNumber() {
        this.setState({
            revalNumber: !this.state.revalNumber
        })
    }

    render() {
        const {
            attachments, user, created_at, product_qty,
            product_price, p_desc, product_meta, p_title, bmeta
        } = this.state.single
        return (
            <Layout>
                <Content>
                    <div className='content'>
                        {
                            this.state.single &&
                            <CommonHead
                                title={p_title ? `${p_title} | ${config.siteName}` : config.siteName}
                                description={bmeta ? `${bmeta}` : config.siteName}
                            />
                        }

                        <Spin spinning={this.state.loading} delay={500}>
                            <Col xs={{span: 24}} lg={{span: 12}}>
                                <div className='postedinfo'>
                                    By {user && user.username}, posted at {moment(created_at).format('LLL')}
                                </div>
                                <div className='pdetails'>
                                    <Carousel className='imageslider'
                                              dotsClass='imgthumb'
                                              customPaging={
                                                  (i) => {
                                                      return (
                                                          <a>
                                                              <img src={`${config.fileurl}${attachments[i].pat_path}`} />
                                                          </a>
                                                      );
                                                  }
                                              }>
                                        {
                                            attachments &&
                                            attachments.map((item, index) => {
                                                return (
                                                    <img key={index} src={`${config.fileurl}${item.pat_path}`}/>
                                                )
                                            })
                                        }
                                    </Carousel>
                                    <div className='productdesc'>
                                        {ReactHtmlParser(p_desc)}
                                    </div>
                                </div>
                            </Col>
                            <Col xs={{span: 24}} lg={{span: 12}}>

                                <div className='productmeta'>
                                    <h3 className='product-title'>{p_title}</h3>
                                    <div className='price-content'>
                                        Tk {product_price}
                                    </div>

                                    <h3>Product Quantity: {product_qty}</h3>
                                    {
                                        product_meta &&
                                        product_meta.map((item, index) => {
                                            return (
                                                <h3 key={index}>{`${item.name}: ${item.value}`}</h3>
                                            )
                                        })
                                    }

                                    <Button onClick={() => this.revalNumber()} type='primary' block icon='phone' size='large'>
                                        {
                                            this.state.revalNumber ?
                                                <span>Click to reval number</span> :
                                                user && user.phone_no
                                        }

                                    </Button>

                                </div>

                            </Col>
                        </Spin>
                    </div>
                </Content>
                <SocialShare/>
                <RelatedProducts pid={this.props.match.params.id}/>
                <FooterLinks/>
                <BottomFooter/>
            </Layout>
        )
    }

}

export default Details