import React, {Component} from 'react'
import {Row, Col, Form, Button, Input, message} from 'antd';
import {postapi} from "../helper";
import {reactLocalStorage} from "reactjs-localstorage";

const FormItem = Form.Item;

class UpdateProfileForm extends Component {
    constructor(props) {
        super(props)
    }

    handleSubmit = (e) => {
        const _this = this

        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                _this.setState({
                    loading: true
                })

                const doregister = postapi('updatebasic', {
                    id: _this.props.single.uid,
                    username: values.userName,
                    email: values.email,
                    password: values.password,
                    fullname: values.fullname,
                    phone: values.phone,
                })

                doregister.then((data) => {
                    if (data.status === 'error') {
                        message.error(data.msg)
                        _this.setState({
                            loading: false
                        })
                    }
                    else {
                        message.success('Your profile successfully updated.')
                    }
                })
            }
        });
    }

    render() {
        const {single} = this.props

        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };

        return (
            <div className='col-inner'>
                <h3>Update Profile</h3>
                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                        {...formItemLayout}
                        label="Username"
                    >
                        {getFieldDecorator('userName', {
                            initialValue: single.username,
                            rules: [{
                                required: true, message: 'Please type your username!',
                            }],
                        })(
                            <Input />
                        )}
                    </FormItem>

                    <FormItem
                        {...formItemLayout}
                        label="E-mail"
                    >
                        {getFieldDecorator('email', {
                            initialValue: single.email,
                            rules: [{
                                type: 'email', message: 'The input is not valid E-mail!',
                            }, {
                                required: true, message: 'Please type your E-mail!',
                            }],
                        })(
                            <Input />
                        )}
                    </FormItem>

                    <FormItem
                        {...formItemLayout}
                        label="Full Name"
                    >
                        {getFieldDecorator('fullname', {
                            initialValue: single.fullname,
                            rules: [{
                                required: true, message: 'Please type your full name.',
                            }],
                        })(
                            <Input />
                        )}
                    </FormItem>

                    <FormItem
                        {...formItemLayout}
                        label="Phone"
                    >
                        {getFieldDecorator('phone', {
                            initialValue: single.phone_no,
                            rules: [{
                                required: true, message: 'Please type your phone.',
                            }],
                        })(
                            <Input />
                        )}
                    </FormItem>

                    <FormItem style={{textAlign: 'right'}}>
                        <Button type='primary' htmlType='submit'>Update</Button>
                    </FormItem>

                </Form>
            </div>
        )
    }

}


const UpdateProfile = Form.create()(UpdateProfileForm);

export default UpdateProfile