import React, {Component} from 'react'
import {Row, Col, Button, Tabs, Modal,} from 'antd';
import Slider from "react-slick";
import { Link } from 'react-router-dom'
import config from "../config";
import SignIn from "./SigninForm";
import SignUp from "./SignupForm";
const TabPane = Tabs.TabPane;

class HomeAbout extends Component {
    constructor(props) {
        super(props)
        this.state = {
            openLoginModal: false,
        }
    }

    toggleLoginModal = (openLoginModal) => {
        this.setState({
            openLoginModal: openLoginModal,
        });
    }

    render() {

        var settings = {
            dots: false,
            infinite: true,
            autoplay: true,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 1,
            initialSlide: 0,
            arrows: false,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                        dots: false,
                        infinite: true,
                        autoplay: true,
                        speed: 500,
                        initialSlide: 0,
                        arrows: false,
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2,
                        dots: false,
                        infinite: true,
                        autoplay: true,
                        speed: 500,
                        initialSlide: 0,
                        arrows: false,
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        dots: false,
                        infinite: true,
                        autoplay: true,
                        speed: 500,
                        initialSlide: 0,
                        arrows: false,
                    }
                }
            ]
        };

        return (
            <Row>
                <Col sm={{span: 24}} lg={{span: 12}}>
                    <div className='col-inner-pd'>
                        <h3>
                            আজ হাট ডট কম, বাংলাদেশে একটি মোবাইল ভিত্তিক ই-মার্কেটপ্লেস।
                        </h3>
                        <div className='cscrwrapper'>
                            <Slider {...settings}>
                                <div>
                                    <h3>1</h3>
                                </div>
                                <div>
                                    <h3>2</h3>
                                </div>
                                <div>
                                    <h3>3</h3>
                                </div>
                                <div>
                                    <h3>4</h3>
                                </div>
                                <div>
                                    <h3>5</h3>
                                </div>
                                <div>
                                    <h3>6</h3>
                                </div>
                                <div>
                                    <h3>7</h3>
                                </div>
                                <div>
                                    <h3>8</h3>
                                </div>
                            </Slider>
                        </div>
                        {
                            (config.isLoggedin && config.user.user_type === 'public') ?
                                <Link to='/posts/addnew' className='ant-btn ant-btn-primary longbtn'>Post Live Ads</Link> :
                                <Button type="primary" className='longbtn' onClick={() => this.toggleLoginModal(true)}>Post Live Ads</Button>
                        }
                    </div>
                </Col>
                <Col sm={{span: 24}} lg={{span: 12}}>
                    <div className='col-inner-pd'>
                        <h3>Cricket World Cup 2019 || Tigers the New Champions</h3>
                        <img className='img-responsive' src={require('../images/aajhat_cricket.gif')}/>
                    </div>
                </Col>
                <Modal
                    visible={this.state.openLoginModal}
                    footer={null}
                    onOk={() => this.toggleLoginModal(false)}
                    onCancel={() => this.toggleLoginModal(false)}
                >
                    <Tabs defaultActiveKey="1">
                        <TabPane tab="Login" key="1">
                            <SignIn {...this.props} SignInModal={() => this.toggleLoginModal(false)}/>
                        </TabPane>
                        <TabPane tab="Register" key="2">
                            <SignUp {...this.props} SignInModal={() => this.toggleLoginModal(false)}/>
                        </TabPane>
                    </Tabs>
                </Modal>
            </Row>
        )
    }

}

export default HomeAbout