import React, {Component} from 'react'
import {Row, Col, Card} from 'antd';
import {postapi} from "../helper";
import config from "../config";
const { Meta } = Card;


class RelatedProducts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            related: [],
            loadingRelated: false
        }
    }

    componentDidMount() {
        this.getrelatedproducts(this.props.pid)
    }


    getrelatedproducts(pid) {
        const _this = this
        _this.setState({
            loadingRelated: true
        })
        const categories = postapi('getrelatedproduct', {
            pid: pid
        })
        categories.then((data) => {
            console.log(data)
            _this.setState({
                related: data.data,
                loadingRelated: false
            })
        })
    }


    render() {
        return (
            <div className='related-products'>
                <Row type="flex" justify="space-around">
                    {
                        this.state.related.map((item, index) => {
                            return (
                                <Col xs={{span: 24}} lg={{span: 6}}>
                                    <a key={index} href={`/product/${item.pid}`}>
                                        <Card
                                            hoverable
                                            cover={<img alt="example" src={config.fileurl+item.attachments[0].pat_path} />}
                                        >
                                            <Meta
                                                title={item.p_title}
                                                description={<h5 className='related-cat-name'>{item.cat_name}</h5>}
                                            />
                                        </Card>
                                    </a>
                                </Col>
                            )
                        })
                    }
                </Row>
            </div>
        )
    }

}

export default RelatedProducts