import React, {Component} from 'react'
import {Row, Col, Layout, Form, Button, Input, List} from 'antd';
import CommonHead from "./CommonHead";
import FooterLinks from "./FooterLinks";
import BottomFooter from "./BottomFooter";
import {postapi} from "../helper";
import config from "../config";
import moment from "moment";
const {Content} = Layout;
const FormItem = Form.Item;

class SpecialCategories extends Component {
    constructor(props) {
        super(props)
        this.state = {
            products: [],
            cat_name: ''
        }
    }

    componentDidMount() {
        console.log(this.props.match.params.scat)
        this.getspecialproducts(this.props.match.params.scat)
    }

    getspecialproducts(slug) {
        const _this = this
        _this.setState({
            loadingproducts: true
        })
        const getproducts = postapi('getspecialproducts', {
            slug: slug
        })
        getproducts.then((data) => {
            console.log(data)
            if (data) {
                _this.setState({
                    products: data.data,
                    cat_name: data.cat_name,
                    loadingproducts: false
                })
            }
            else {
                _this.setState({
                    loadingproducts: false
                })
            }

        })

    }

    render() {

        return (
            <Layout>
                <Content>
                    <div className='content'>
                        <CommonHead/>
                        <Row>
                            <Col xs={{span: 24}} lg={{span: 6}}>
                                Ads
                            </Col>
                            <Col xs={{span: 24}} lg={{span: 12}}>
                                <h3>Product From: {this.state.cat_name.cat_name_en}</h3>
                                <List
                                    loading={this.state.loadingproducts}
                                    itemLayout="vertical"
                                    size="large"
                                    pagination={{
                                        pageSize: 15,
                                    }}
                                    dataSource={this.state.products}
                                    renderItem={item => {
                                        return (
                                            <a href={`/product/${item.pid}`} className='product-card'>
                                                <Row>
                                                    <Col xs={{span: 24}} lg={{span: 6}}>
                                                        <div className='cover'>
                                                            <img alt={item.p_title}
                                                                 src={config.fileurl + item.attachments[0].pat_path}/>
                                                        </div>

                                                    </Col>
                                                    <Col xs={{span: 24}} lg={{span: 12}}>
                                                        <div className='product-desc'>
                                                            <h2>{item.p_title}</h2>
                                                            <h4>{item.cat_name}</h4>
                                                            <h3>Tk {item.product_price}</h3>
                                                        </div>
                                                    </Col>
                                                </Row>
                                                <div className='productcard-footer'>
                                                    <span>{this.time_ago(moment(item.created_at).valueOf())}</span>
                                                </div>
                                            </a>
                                        )
                                    }}
                                />
                            </Col>
                            <Col xs={{span: 24}} lg={{span: 6}}>
                                Ads
                            </Col>
                        </Row>
                    </div>
                </Content>
                <FooterLinks/>
                <BottomFooter/>
            </Layout>
        )
    }

}

export default SpecialCategories