import React, {Component} from 'react'
import {Layout,
    Form, Icon, Input, Button, Modal, message
} from 'antd';
import {Redirect, Link} from "react-router-dom";
import {Helmet} from "react-helmet";
import {reactLocalStorage} from "reactjs-localstorage";
import config from "../config";
import {postapi} from "../helper";
const FormItem = Form.Item;

class LoginForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            redirectToReferrer: false,
            modal2Visible: true,
            loading: false
        }
    }

    setModal2Visible(modal2Visible) {
        this.setState({ modal2Visible });
    }

    handleSubmit = (e) => {
        var _this = this
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {

                _this.setState({
                    loading: true
                })
                const dologin = postapi('adminlogin', {
                    email: values.userName,
                    password: values.password
                })

                dologin.then((data) => {
                    setTimeout(() => {
                        if (data.status === 'error') {
                            message.error(data.msg)
                            _this.setState({
                                loading: false
                            })
                        }
                        else {
                            reactLocalStorage.setObject('user', data.data)
                            reactLocalStorage.setObject('islogged', true)
                            _this.setState({
                                loading: false,
                            }, () => {
                                window.location.href = '/admin';
                            })
                        }
                    }, 2000)
                })
            }
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        let { from } = this.props.location.state || { from: { pathname: "/" } };
        let { redirectToReferrer } = this.state;

        if (redirectToReferrer) return <Redirect to={from} />;
        return (
            <Layout>
                <Helmet>
                    <title>Login | AajHat</title>
                </Helmet>
                <Modal
                    closable={false}
                    maskClosable={false}
                    footer={null}
                    title={<div className='login-logo'><img src={config.sitelogo}/></div>}
                    centered
                    visible={this.state.modal2Visible}
                    onOk={() => this.setModal2Visible(false)}
                    onCancel={() => this.setModal2Visible(false)}
                >
                    <Form onSubmit={this.handleSubmit} className="login-form">
                        <FormItem>
                            {getFieldDecorator('userName', {
                                rules: [{ required: true, message: 'Please input your username!' }],
                            })(
                                <Input type='text' prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Username" />
                            )}
                        </FormItem>
                        <FormItem>
                            {getFieldDecorator('password', {
                                rules: [{ required: true, message: 'Please input your Password!' }],
                            })(
                                <Input type='password' prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
                            )}
                        </FormItem>
                        <FormItem>
                            <Button disabled={this.state.loading} loading={this.state.loading} type="primary" htmlType="submit" className="login-form-button">
                                Log in
                            </Button>
                        </FormItem>
                    </Form>
                </Modal>

            </Layout>
        )
    }

}

const Login = Form.create()(LoginForm);

export default Login