import React, {Component} from 'react'
import {Row, Col, Layout, Icon} from 'antd';
import {Link} from "react-router-dom";
import config from "../config";
const {Footer} = Layout;

class BottomFooter extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <Footer>
                <Row>
                    <Col sm={{span: 24}} md={{span: 8}}>
                        <div className='footer-menu'>
                            <h4>Contact Us</h4>
                            <ul>
                                <li><Link to='/'><Icon type="mobile" theme="outlined" /> +88 01798092964</Link></li>
                                <li><Link to='/'><Icon type="mail" theme="outlined" /> info@aajhat.com</Link></li>
                                <li><Link to='/'><Icon type="home" theme="outlined" /> House: 56 Road: 02, Sector: 03,Uttara, Dhaka-1230.</Link></li>
                            </ul>
                        </div>
                    </Col>
                    <Col sm={{span: 24}} md={{span: 8}}>
                        <div className='footer-menu centered'>
                            <h4>Connect with us</h4>
                            <ul className='social-icons'>
                                <li><a href='/'><Icon type="facebook" theme="outlined" /></a></li>
                                <li><a href='/'><Icon type="twitter" theme="outlined" /></a></li>
                                <li><a href='/'><Icon type="linkedin" theme="outlined" /></a></li>
                                <li><a href='/'><Icon type="instagram" theme="outlined" /></a></li>
                                <li><a href='/'><Icon type="google-plus" theme="outlined" /></a></li>
                            </ul>
                        </div>
                    </Col>
                    <Col sm={{span: 24}} md={{span: 8}}>
                        <div className='footer-menu centered'>
                            <h4>{config.copyright}</h4>
                            <img className='footer-logo' src={config.sitelogo}/>
                        </div>
                    </Col>
                </Row>
            </Footer>
        )
    }

}

export default BottomFooter