import React, {Component} from 'react'
import {Row, Col, Input, Layout, Icon} from 'antd';
import CommonHead from "./CommonHead";
import {callapi, postapi} from "../helper";
import {Link, Route, Redirect} from 'react-router-dom'
import config from "../config";
import FooterLinks from "./FooterLinks";
import BottomFooter from "./BottomFooter";
import ListAds from "./ListAds";

const {Content} = Layout;

const Search = Input.Search;

const IconText = ({type, text}) => (
    <span>
    <Icon type={type} style={{marginRight: 8}}/>
        {text}
  </span>
);

class AllAds extends Component {
    constructor(props) {
        super(props)
        this.state = {
            categories: [],
            loadingproducts: false,
            searchText: ''
        }
    }

    componentDidMount() {
        if (!this.state.searchText.length > 0) {
            this.getCategories()
        }

    }

    getCategories() {
        const _this = this
        const categories = postapi('getcategories', {
            ignore_special: true
        })
        categories.then((data) => {
            _this.setState({
                categories: data.data
            })
        })
    }

    onSearch(value) {
        this.setState({
            searchText: value
        })
        this.props.history.push('/ads/all/?s='+value)
    }

    time_ago(time) {

        switch (typeof time) {
            case 'number':
                break;
            case 'string':
                time = +new Date(time);
                break;
            case 'object':
                if (time.constructor === Date) time = time.getTime();
                break;
            default:
                time = +new Date();
        }
        var time_formats = [
            [60, 'seconds', 1], // 60
            [120, '1 minute ago', '1 minute from now'], // 60*2
            [3600, 'minutes', 60], // 60*60, 60
            [7200, '1 hour ago', '1 hour from now'], // 60*60*2
            [86400, 'hours', 3600], // 60*60*24, 60*60
            [172800, 'Yesterday', 'Tomorrow'], // 60*60*24*2
            [604800, 'days', 86400], // 60*60*24*7, 60*60*24
            [1209600, 'Last week', 'Next week'], // 60*60*24*7*4*2
            [2419200, 'weeks', 604800], // 60*60*24*7*4, 60*60*24*7
            [4838400, 'Last month', 'Next month'], // 60*60*24*7*4*2
            [29030400, 'months', 2419200], // 60*60*24*7*4*12, 60*60*24*7*4
            [58060800, 'Last year', 'Next year'], // 60*60*24*7*4*12*2
            [2903040000, 'years', 29030400], // 60*60*24*7*4*12*100, 60*60*24*7*4*12
            [5806080000, 'Last century', 'Next century'], // 60*60*24*7*4*12*100*2
            [58060800000, 'centuries', 2903040000] // 60*60*24*7*4*12*100*20, 60*60*24*7*4*12*100
        ];
        var seconds = (+new Date() - time) / 1000,
            token = 'ago',
            list_choice = 1;

        if (seconds === 0) {
            return 'Just now'
        }
        if (seconds < 0) {
            seconds = Math.abs(seconds);
            token = 'from now';
            list_choice = 2;
        }
        var i = 0,
            format;
        while (format = time_formats[i++])
            if (seconds < format[0]) {
                if (typeof format[2] === 'string')
                    return format[list_choice];
                else
                    return Math.floor(seconds / format[2]) + ' ' + format[1] + ' ' + token;
            }
        return time;
    }

    render() {
        // if (this.state.searchText.length > 0) {
        //     return <Redirect push to={{pathname: '?s='+this.state.searchText}} />
        // }
        return (
            <Layout>
                <Content>
                    <div className='content'>
                        <CommonHead
                            title='Aajhat | All ads'
                            description='All ads'
                        />
                        <Row>
                            <Col xs={{span: 24}} lg={{span: 6}}></Col>
                            <Col xs={{span: 24}} lg={{span: 12}}>
                                <div className='header-search'>
                                    <Search
                                        placeholder="Search . . ."
                                        enterButton={<Icon type='search'/>}
                                        size="large"
                                        onSearch={value => this.onSearch(value)}
                                    />
                                </div>
                            </Col>
                            <Col xs={{span: 24}} lg={{span: 6}}></Col>
                        </Row>
                        <Row>
                            <Col xs={{span: 24}} lg={{span: 6}}>
                                {/*<div className='category-header'>*/}
                                {/*<h2>All Categories</h2>*/}
                                {/*</div>*/}
                                <ul className='categories-list'>
                                    {
                                        this.state.categories.map((item, index) => {
                                            return (
                                                <li key={index}>
                                                    <Link to={`/ads/${item.cat_slug}`}>
                                                        <img src={config.fileurl + item.cat_icon}/>
                                                        <span>{item.cat_name_en}</span>
                                                    </Link>
                                                </li>
                                            )
                                        })
                                    }
                                </ul>

                            </Col>
                            <Col xs={{span: 24}} lg={{span: 12}}>
                                <div className='all-products'>
                                    <Route
                                        key='/ads/all'
                                        path={`/ads/all`}
                                        render={(props) => {
                                            return <ListAds {...props} search={this.state.searchText}/>
                                        }}
                                    />
                                    {
                                        this.state.categories.map((item, index) => (<Route
                                            key={index}
                                            path={`/ads/${item.cat_slug}`}
                                            component={ListAds}
                                        />))
                                    }

                                </div>
                            </Col>
                            <Col xs={{span: 24}} lg={{span: 6}}>
                                Ads
                            </Col>
                        </Row>
                    </div>
                </Content>
                <FooterLinks/>
                <BottomFooter/>
            </Layout>
        )
    }

}

export default AllAds