import React, {Component} from 'react'
import {
    Row, Col, Table, Icon,
    Divider, Modal, Button,
    Drawer, Form, Input, Select, message, Upload, Switch
} from 'antd';
import {callapi, postapi} from "../helper";
import moment from 'moment'
import Uploader from "./Uploader";
import config from "../config";
import {Link} from "react-router-dom";
const ButtonGroup = Button.Group;
const FormItem = Form.Item;
const Option = Select.Option;

class CategoriesWrapper extends Component {
    constructor(props) {
        super(props)

        this.state = {
            addPlan: false,
            categories: [],
            addCategory: false,

            cat_name_en: '',
            cat_name_bn: '',
            cat_icon: '',
            parent_id: '',
            is_special: false,

            isSingle: false,
            single_id: ''
        }
    }

    componentWillMount() {
        this.getCategories()
    }

    getCategories() {
        //getcategories
        const _this = this
        _this.setState({
            loading: true
        })
        const categories = callapi('getcategories')
        categories.then((data) => {
            _this.setState({
                categories: data
            })
        })
    }

    onChangeSP = (checked) => {
        this.setState({
            is_special: checked
        })
    }

    showDrawer = () => {
        this.setState({
            addCategory: true,
        });
    };

    onClose = () => {
        this.setState({
            addCategory: false,
            isSingle: false,
            cat_name_en: '',
            cat_name_bn: '',
            cat_icon: '',
            parent_id: '',
            is_special: false,
            single_id: '',
        });

    };

    handleChange(name, event) {
        this.setState({
            [name]: event.target.value
        })
    }

    saveecategory() {
        const _this = this;
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                const addcat = postapi('addcategory', {
                    cat_name_en: this.state.cat_name_en,
                    cat_name_bn: this.state.cat_name_bn,
                    cat_icon: this.state.cat_icon,
                    parent_id: this.state.parent_id ? this.state.parent_id : 0,
                    is_special: this.state.is_special ? 1 : 0,
                    created_at: moment().toISOString(),
                })

                addcat.then((data) => {
                    if (data) {
                        if (data.status === 'error') {
                            message.error(data.msg)
                            _this.setState({
                                loading: false
                            })
                        }
                        else {
                            _this.setState({
                                addCategory: false,
                                loading: false,
                            })
                            message.success(data.msg)
                            _this.getCategories()
                            _this.props.form.resetFields()
                        }
                    }
                    else {
                        message.warning("API error");
                    }

                })

            }
        });
    }

    updatecategory() {
        const _this = this;
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                const updatecet = postapi('updatecategory', {
                    cat_id: this.state.single_id,
                    cat_name_en: this.state.cat_name_en,
                    cat_name_bn: this.state.cat_name_bn,
                    cat_icon: this.state.cat_icon,
                    parent_id: this.state.parent_id ? this.state.parent_id : 0,
                    is_special: this.state.is_special ? 1 : 0,
                    updated_at: moment().toISOString(),
                })

                updatecet.then((data) => {
                    if (data) {
                        if (data.status === 'error') {
                            message.error(data.msg)
                            _this.setState({
                                loading: false
                            })
                        }
                        else {
                            _this.setState({
                                addCategory: false,
                                loading: false,
                            })
                            message.success(data.msg)
                            _this.getCategories()
                            _this.props.form.resetFields()
                        }
                    }
                    else {
                        message.warning("API error");
                    }

                })

            }
        });
    }

    deletecategory() {
        const _this = this;
        const deleteplan = postapi('deletecategory', {
            cat_id: this.state.single_id,
        })

        deleteplan.then((data) => {
            if (data.status === 'error') {
                message.error(data.msg)
                _this.setState({
                    loading: false
                })
            }
            else {
                _this.setState({
                    addCategory: false,
                    loading: false,
                })
                message.success(data.msg)
                _this.getCategories()
                _this.props.form.resetFields()
            }
        })
    }

    getimgurl = (fileinfo) => {
        this.setState({ cat_icon: fileinfo.response.results.data })
    }

    render() {
        let _this = this
        const {getFieldDecorator} = this.props.form;
        const columns = [
            {
                title: 'Category Name EN',
                dataIndex: 'cat_name_en',
                key: 'cat_name_en',
            },
            {
                title: 'Category Name BN',
                dataIndex: 'cat_name_bn',
                key: 'cat_name_bn',
            },
            {
                title: 'Icon',
                dataIndex: 'cat_icon',
                key: 'cat_icon',
                render: (text) => {
                    if (text.length > 0){
                        return <img style={{width: 50}} src={`${config.fileurl}${text}`} />
                    }
                    else {
                        return <span>No icon</span>
                    }
                }
            },
            {
                title: 'Action',
                key: 'action',
                render: (text, record) => {
                    return (
                        <span>
                        <Divider type="vertical"/>
                        <Button onClick={() => {
                            _this.setState({
                                isSingle: true,
                                single_id: record.cat_id,
                                cat_name_en: record.cat_name_en,
                                cat_name_bn: record.cat_name_bn,
                                cat_icon: record.cat_icon,
                            }, () => {
                                _this.showDrawer()
                            })

                        }} size='small' type="primary">
                            <Icon type="edit"/>
                        </Button>
                        <Divider type="vertical"/>
                        <Button onClick={() => {
                            _this.setState({
                                single_id: record.cat_id,
                            }, () => {
                                _this.deletecategory()
                            })

                        }} size='small' type="danger">
                            <Icon type="delete"/>
                        </Button>
                    </span>
                    )
                },
            }
        ];

        return (
            <div>
                <Row>
                    <Col lg={24}>
                        <div className='helper-bar'>
                            <Button type="primary" onClick={this.showDrawer}>Add new Category</Button>
                            <Link to='/categoriesmeta' className='ant-btn ant-btn-primary'>Add Category Meta</Link>
                        </div>
                        <Drawer
                            title={this.state.isSingle ? 'Update category' : 'Add new category'}
                            width={520}
                            className="add_new_category"
                            closable={false}
                            onClose={this.onClose}
                            visible={this.state.addCategory}
                        >
                            <div>
                                <Form>

                                    <FormItem
                                        label="Category Name EN"
                                    >
                                        {getFieldDecorator('cat_name_en', {
                                            rules: [
                                                {
                                                    required: true, message: 'You must type category name!',
                                                }
                                            ],
                                            initialValue: this.state.cat_name_en
                                        })(
                                            <Input
                                                onChange={(e) => this.handleChange('cat_name_en', e)}
                                                type='text'/>
                                        )}
                                    </FormItem>

                                    <FormItem
                                        label="Category Name BN"
                                    >
                                        {getFieldDecorator('cat_name_bn', {
                                            rules: [
                                                {
                                                    required: true, message: 'You must type category name!',
                                                }
                                            ],
                                            initialValue: this.state.cat_name_bn
                                        })(
                                            <Input
                                                onChange={(e) => this.handleChange('cat_name_bn', e)}
                                                type='text'/>
                                        )}
                                    </FormItem>

                                    <FormItem
                                        label="Parent"
                                    >
                                        {getFieldDecorator('parent_id', {
                                            initialValue: this.state.parent_id
                                        })(
                                            <Select
                                                placeholder='Select Parent'
                                            >
                                                {
                                                    this.state.categories.map((item, index) => {
                                                        return <Option key={index} value={item.cat_id}>{item.cat_name_en}</Option>
                                                    })
                                                }
                                            </Select>
                                        )}
                                    </FormItem>

                                    <FormItem
                                        label='Is Special'
                                    >
                                        <Switch defaultChecked={this.state.is_special} onChange={this.onChangeSP} />
                                    </FormItem>

                                    <div style={{marginTop: 20, marginBottom: 20}}>
                                        <Upload
                                            accept="image/*"
                                            name='categories'
                                            action={`${config.apiurl}uploadimage`}
                                            data={{
                                                folder: 'categories'
                                            }}
                                            showUploadList={false}
                                            defaultFileList={[]}

                                            onChange={(info) => {
                                                _this.setState({
                                                    uploading: true
                                                })
                                                const status = info.file.status;

                                                if (status === 'done') {
                                                    const resp = info.file.response.results;
                                                    if (resp.status === 'success') {
                                                        message.success(`${info.file.originFileObj.name} file uploaded successfully.`);
                                                        _this.setState({
                                                            uploading: false,
                                                            cat_icon: resp.data
                                                        })
                                                    }
                                                    else {
                                                        message.error(`${info.file.originFileObj.name} - ${resp.msg}.`);
                                                        _this.setState({
                                                            uploading: false
                                                        })
                                                    }
                                                } else if (status === 'error') {
                                                    message.error(`${info.file.originFileObj.name} file upload failed.`);
                                                    _this.setState({
                                                        uploading: false
                                                    })
                                                }
                                            }}
                                        >
                                            <Button style={{width: '100%'}}>
                                                <Icon type="upload" /> Upload Featured Image
                                            </Button>
                                        </Upload>
                                        <div className='imgPreviewer'>
                                            {
                                                this.state.cat_icon &&
                                                <img style={{width: '30%'}} src={config.fileurl+this.state.cat_icon}/>
                                            }
                                        </div>
                                    </div>

                                    <FormItem>
                                        <Button
                                            onClick={() => {
                                                this.state.isSingle ?
                                                    this.updatecategory() :
                                                    this.saveecategory()

                                            }}
                                            type="primary" htmlType="submit">{this.state.isSingle ? 'Update category' : 'Save category'}</Button>
                                    </FormItem>
                                </Form>
                            </div>
                        </Drawer>

                    </Col>
                </Row>
                <Row>
                    <div style={{backgroundColor: '#fff', padding: 20}}>
                        <Table size='small' columns={columns} dataSource={this.state.categories}/>
                    </div>

                </Row>
            </div>
        )
    }

}

const Categories = Form.create()(CategoriesWrapper);

export default Categories