import React, {Component} from 'react'
import {Row, Col, Button, Table, Divider, Icon, message} from 'antd';
import {Link} from 'react-router-dom'
import {postapi} from "../helper";


class ManagePosts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            products: [],
            loadingproducts: false
        }
    }

    componentDidMount() {
        this.getProducts()
    }

    getProducts(slug = false) {
        const _this = this
        _this.setState({
            loadingproducts: true
        })
        const getproducts = postapi('getproducts', slug)
        getproducts.then((data) => {
            if (data) {
                _this.setState({
                    products: data.data,
                    loadingproducts: false
                })
            }
            else {
                _this.setState({
                    loadingproducts: false
                })
            }

        })

    }

    deleteTrack(id){
        const _this = this
        const deletetrack = postapi('deleteproduct', {
            pid: id
        })

        deletetrack.then((data) => {
            if (data.status === 'error') {
                message.error(data.msg)
            }
            else {
                message.success(data.msg)
                this.getProducts()
            }
        })
    }

    render() {

        const columns = [
            {
                title: 'Title',
                dataIndex: 'p_title',
                key: 'p_title',
            },
            {
                title: 'Price',
                dataIndex: 'product_price',
                key: 'product_price',
            },
            {
                title: 'Address',
                dataIndex: 'product_address',
                key: 'product_address',
            },
            {
                title: 'Actions',
                dataIndex: 'actions',
                key: 'actions',
                render: (text, record) => {
                    return (
                        <div>
                            <Link className='ant-btn ant-btn-icon-only'
                                  to={{
                                      pathname: "/editpost",
                                      search: "?id="+record.key,
                                  }}><Icon type='edit'/></Link>
                            <Divider type="vertical" />
                            <Button type='danger' icon='delete' onClick={() => this.deleteTrack(record.pid)}/>
                        </div>
                    )
                }
            },
        ];

        return (
            <div>
                <div className='helper-bar'>
                    <Link to='/addpost' className='ant-btn ant-btn-primary'>Add New Product</Link>
                </div>

                <Table size='small' loading={this.state.loadingproducts} columns={columns} dataSource={this.state.products} />

            </div>
        )
    }

}

export default ManagePosts