import React, {Component} from 'react'
import {Row, Col, Input, Select, Button, message, Upload, Icon, Form, Table, Layout} from 'antd'
import ReactQuill from 'react-quill';
import moment from 'moment'
import Uploader from "./Uploader";
import {Link} from "react-router-dom";
import {callapi, postapi} from "../helper";
import config from "../config";
import CommonHead from "./CommonHead";
import FooterLinks from "./FooterLinks";
import BottomFooter from "./BottomFooter";
const FormItem = Form.Item;
const Option = Select.Option;
const {Content} = Layout;

class AddPostWrapper extends Component {
    constructor(props) {
        super(props)

        this.state = {
            title: '',
            tags: [],
            selectedtags: [],
            description: '',
            categoty: '',
            product_price: '',
            product_qty: '',
            product_brand: '',
            product_condition: '',
            thumbimg: [],
            state_id: '',
            city_id: '',
            area_id: '',

            categories: [],
            districts: [],
            cities: [],
            categorymeta: [],
            productmeta: [],
            areas: [],
        }
        this.handleChangetags = this.handleChangetags.bind(this)
        this.handleChangedescription = this.handleChangedescription.bind(this)
        this.handleChangecategoty = this.handleChangecategoty.bind(this)

    }

    componentDidMount() {
        this.getCategories()
        this.getStates()
        this.getCities()
        this.gettags()
    }

    getCategories() {
        const _this = this
        const categories = callapi('getcategories')
        categories.then((data) => {
            _this.setState({
                categories: data
            })
        })
    }

    getStates() {
        const _this = this
        const categories = callapi('getstates')
        categories.then((data) => {
            _this.setState({
                districts: data
            })
        })
    }

    getCities() {
        const _this = this
        const categories = callapi('getcities')
        categories.then((data) => {
            _this.setState({
                cities: data
            })
        })
    }

    gettags() {
        const _this = this
        const categories = callapi('gettags')
        categories.then((data) => {
            _this.setState({
                tags: data
            })
        })
    }

    handleChange(name, event) {
        this.setState({ [name]: event.target.value })
    }

    handleChangetags(value) {
        this.setState({ selectedtags: value })
    }

    handleChangedescription(value) {
        this.setState({ description: value })
    }

    handleChangecategoty(value) {
        const _this = this
        _this.setState({
            categoty: value,
            productmeta: []
        }, () => {
            _this.getCategoryMeta(value)
        })
    }

    handleChangeSelect(name, value) {
        this.setState({
            [name]: value,
        })
    }

    handleChangeCity(value) {
        this.setState({
            city_id: value,
        }, () => {
            this.getArea(value)
        })
    }

    getCategoryMeta(catid){
        const _this = this
        const categorymeta = postapi('getcategorymetabycatid', {
            cat_id: catid,
        })
        categorymeta.then((data) => {
            _this.setState({
                categorymeta: data.data
            })
        })
    }

    getArea(parentid) {
        const _this = this

        const categories = postapi('getarea', {
            parent_id: parentid
        })
        categories.then((data) => {
            _this.setState({
                areas: data.data,
            })
        })
    }

    handleTextMetaChange = (name, event) => {
        event.persist();
        this.setState(prevState => ({
            productmeta : {
                ...prevState.productmeta,
                [name]: event.target.value
            }
        }))
    }

    handleChangemeta = (name, value) => {
        this.setState(prevState => ({
            productmeta : {
                ...prevState.productmeta,
                [name]: value
            }
        }))
    }

    generateThumbImages(){
        return this.state.thumbimg.map((item, index) => {
            return (
                <Col key={index} span={6}>
                    <Icon style={{position: 'absolute', right: -5, top: -5}} type='close'
                          onClick={() => {this.removeimg(index)}}
                    />
                    <img style={{width: '100%'}} src={config.fileurl+item}/>
                </Col>
            )
        })
    }

    removeimg(index){
        this.setState({
            thumbimg: this.state.thumbimg.filter((_, i) => i !== index),
        });
    }

    savepost(){
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {

                let pdata = {
                    uid: config.user.uid,
                    cat_id: parseInt(this.state.categoty),
                    created_at: this.state.created_at,
                    p_desc: this.state.description,
                    product_address: this.state.product_address,
                    product_brand: this.state.product_brand,
                    state_id: parseInt(this.state.state_id),
                    city_id: parseInt(this.state.city_id),
                    area_id: parseInt(this.state.area_id),
                    product_condition: this.state.product_condition,
                    product_price: this.state.product_price,
                    product_qty: parseInt(this.state.product_qty),
                    productmeta: this.state.productmeta,
                    selectedtags: this.state.selectedtags,
                    thumbimg: this.state.thumbimg,
                    title: this.state.title,
                }

                pdata['created_at'] = moment().toISOString();
                pdata['updated_at'] = moment().toISOString();

                let addpost = postapi('addproduct', pdata)
                addpost.then((data) => {
                    if (data.status === 'success') {
                        this.setState({
                            title: '',
                            tags: [],
                            selectedtags: [],
                            description: '',
                            categoty: '',
                            product_price: '',
                            product_qty: '',
                            product_brand: '',
                            product_condition: '',
                            thumbimg: [],
                            state_id: '',
                            city_id: '',
                            area_id: '',
                        })
                        message.success('Product successfully added.');
                    }
                    else {
                        message.error(`${data.msg}.`);
                    }
                })

            }
        })

    }

    render() {
        let _this = this
        const {getFieldDecorator} = this.props.form;
        return (
            <Layout>
                <Content>
                    <div className='content'>
                        <CommonHead/>
                        <Row>
                            <div className='helper-bar'>
                                <Link to='/posts/my-posts' className='ant-btn ant-btn-primary'>Back</Link>
                            </div>
                            <Form>
                                <Row className='row-extrapd'>

                                    <Col xs={{ span: 24}} lg={{ span: 12}} className='col-extrapd'>
                                        <div className='tbpd15'>
                                            <FormItem
                                                label="Title"
                                            >
                                                {getFieldDecorator('title', {
                                                    rules: [
                                                        {
                                                            required: true, message: 'You must type title!',
                                                        }
                                                    ],
                                                    initialValue: this.state.title
                                                })(
                                                    <Input
                                                        name='title'
                                                        size='large'
                                                        placeholder="Title"
                                                        onChange={(e) => this.handleChange('title', e)}
                                                        type='text'/>
                                                )}
                                            </FormItem>

                                        </div>
                                        <ReactQuill
                                            value={this.state.description}
                                            modules={{
                                                toolbar: [
                                                    [{ font: [] }, { size: [] }],
                                                    [{ align: [] }, 'direction' ],
                                                    [{ color: [] }, { background: [] }],
                                                    [{ script: 'super' }, { script: 'sub' }],
                                                    ['code-block'],
                                                    ['bold', 'italic', 'underline','strike', 'blockquote'],
                                                    [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
                                                    [ 'link', 'image', 'video' ],
                                                    ['clean']
                                                ],
                                            }}
                                            formats={[
                                                'header', 'font', 'size', 'color', 'background', 'script', 'align', 'direction',
                                                'bold', 'italic', 'underline', 'strike', 'blockquote', 'code-block',
                                                'list', 'ordered', 'bullet', 'indent',
                                                'link', 'image', 'video'
                                            ]}
                                            onChange={this.handleChangedescription} />
                                    </Col>

                                    <Col xs={{ span: 24}} lg={{ span: 6}}>
                                        <div className='col-inner'>
                                            <FormItem
                                                label="Select Category"
                                            >
                                                <Select
                                                    showSearch
                                                    className='form-control'
                                                    placeholder="Select Category"
                                                    optionFilterProp="children"
                                                    onChange={this.handleChangecategoty}
                                                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                                >
                                                    {
                                                        this.state.categories.map((item, index) => {
                                                            return <Option key={index} value={item.cat_id}>{item.cat_name_en}</Option>
                                                        })
                                                    }
                                                </Select>
                                            </FormItem>

                                            <FormItem
                                                label="Product Price"
                                            >
                                                {getFieldDecorator('product_price', {
                                                    rules: [
                                                        {
                                                            required: true, message: 'You must enter product price!',
                                                        }
                                                    ],
                                                    initialValue: this.state.product_price
                                                })(
                                                    <Input
                                                        name='product_price'
                                                        placeholder="Product Price"
                                                        onChange={(e) => this.handleChange('product_price', e)}
                                                        type='number'/>
                                                )}
                                            </FormItem>

                                            <FormItem
                                                label="Product Quantity"
                                            >
                                                {getFieldDecorator('product_qty', {
                                                    initialValue: this.state.product_qty
                                                })(
                                                    <Input
                                                        name='product_price'
                                                        placeholder="Product Quantity"
                                                        onChange={(e) => this.handleChange('product_qty', e)}
                                                        type='number'/>
                                                )}
                                            </FormItem>

                                            <FormItem
                                                label="Product Brand"
                                            >
                                                {getFieldDecorator('product_brand', {
                                                    initialValue: this.state.product_brand
                                                })(
                                                    <Input
                                                        name='product_brand'
                                                        placeholder="Product Brand"
                                                        onChange={(e) => this.handleChange('product_brand', e)}
                                                        type='text'/>
                                                )}
                                            </FormItem>

                                        </div>
                                    </Col>
                                    <Col xs={{span: 24}} lg={{span: 6}}>
                                        <div className='col-inner'>

                                            <FormItem
                                                label="Select District"
                                            >
                                                {getFieldDecorator('state_id', {
                                                    rules: [
                                                        {
                                                            required: true, message: 'You must select district!',
                                                        }
                                                    ],
                                                    initialValue: this.state.state_id
                                                })(
                                                    <Select
                                                        onChange={(value) => this.handleChangeSelect('state_id', value)}
                                                    >
                                                        {
                                                            this.state.districts.map((item, index) => {
                                                                return <Option key={index} value={item.city_id}>{item.city_name}</Option>
                                                            })
                                                        }
                                                    </Select>
                                                )}
                                            </FormItem>

                                            <FormItem
                                                label="Select city"
                                            >
                                                {getFieldDecorator('city_id', {
                                                    rules: [
                                                        {
                                                            required: true, message: 'You must select city!',
                                                        }
                                                    ],
                                                    initialValue: this.state.city_id
                                                })(
                                                    <Select
                                                        placeholder="Select City"
                                                        onChange={(value) => this.handleChangeCity(value)}
                                                    >
                                                        {
                                                            this.state.cities.map((item, index) => {
                                                                return <Option key={index} value={item.city_id}>{item.city_name}</Option>
                                                            })
                                                        }
                                                    </Select>
                                                )}
                                            </FormItem>

                                            <FormItem
                                                label="Select Area"
                                            >
                                                {getFieldDecorator('area_id', {
                                                    rules: [
                                                        {
                                                            required: true, message: 'You must select area!',
                                                        }
                                                    ],
                                                    initialValue: this.state.area_id
                                                })(
                                                    <Select
                                                        placeholder="Select Area"
                                                        onChange={(value) => this.handleChangeSelect('area_id', value)}
                                                    >
                                                        {
                                                            this.state.areas.map((item, index) => {
                                                                return <Option key={index} value={item.city_id}>{item.city_name}</Option>
                                                            })
                                                        }
                                                    </Select>
                                                )}
                                            </FormItem>

                                            <FormItem
                                                label="Address"
                                            >
                                                {getFieldDecorator('product_address', {
                                                    rules: [
                                                        {
                                                            required: true, message: 'Please enter product address!',
                                                        }
                                                    ],
                                                    initialValue: this.state.product_address
                                                })(
                                                    <Input
                                                        name='product_address'
                                                        placeholder="Product Address"
                                                        onChange={(e) => this.handleChange('product_address', e)}
                                                        type='text'/>
                                                )}
                                            </FormItem>

                                        </div>
                                    </Col>
                                </Row>

                                <Row>
                                    {
                                        this.state.categorymeta.map((catmeta, index) => {
                                            if (catmeta.meta_type === 'option') {
                                                return (
                                                    <Col
                                                        key={index} xs={{span: 24}} lg={{span: 6}}>
                                                        <div className='col-inner'>
                                                            <FormItem
                                                                label={catmeta.meta_name}
                                                            >
                                                                <Select
                                                                    placeholder={catmeta.meta_name}
                                                                    onChange={(event) => {this.handleChangemeta(catmeta.meta_id, event)}}
                                                                    value={this.state.productmeta[catmeta.meta_id]}
                                                                >
                                                                    <Option key='' value=''>Select {catmeta.meta_name}</Option>
                                                                    {
                                                                        catmeta.meta_value.map((valitem, valindex) => {
                                                                            return <Option key={valindex} value={valitem}>{valitem}</Option>
                                                                        })
                                                                    }
                                                                </Select>
                                                            </FormItem>

                                                        </div>
                                                    </Col>
                                                )
                                            }
                                            else {
                                                return (
                                                    <Col
                                                        key={index} xs={{span: 24}} lg={{span: 6}}>
                                                        <div className='col-inner'>
                                                            <FormItem
                                                                label={catmeta.meta_name}
                                                            >
                                                                {getFieldDecorator(catmeta.meta_id, {
                                                                    rules: [
                                                                        {
                                                                            required: true, message: 'You must type '+catmeta.meta_name+'!',
                                                                        }
                                                                    ],
                                                                    initialValue: this.state.productmeta[catmeta.meta_id]
                                                                })(
                                                                    <Input
                                                                        name={catmeta.meta_key}
                                                                        placeholder={catmeta.meta_name}
                                                                        onChange={(event) => {this.handleTextMetaChange(catmeta.meta_id, event)}}
                                                                        type='text'/>
                                                                )}
                                                            </FormItem>
                                                        </div>
                                                    </Col>
                                                )
                                            }
                                        })
                                    }
                                </Row>

                                <Row>
                                    <Col xs={{span: 24}} lg={{span: 8}}>
                                        <div className='col-inner'>
                                            <Upload
                                                accept="image/*"
                                                name='product'
                                                action={`${config.apiurl}uploadimage`}
                                                data={{
                                                    folder: 'product'
                                                }}
                                                showUploadList={false}
                                                defaultFileList={[]}
                                                onChange={(info) => {
                                                    _this.setState({
                                                        uploading: true
                                                    })
                                                    const status = info.file.status;

                                                    if (status === 'done') {
                                                        const resp = info.file.response.results;
                                                        if (resp.status === 'success') {
                                                            message.success(`${info.file.originFileObj.name} file uploaded successfully.`);
                                                            _this.setState({
                                                                uploading: false,
                                                                thumbimg: _this.state.thumbimg.concat(resp.data)
                                                            })
                                                        }
                                                        else {
                                                            message.error(`${info.file.originFileObj.name} - ${resp.msg}.`);
                                                            _this.setState({
                                                                uploading: false
                                                            })
                                                        }
                                                    } else if (status === 'error') {
                                                        message.error(`${info.file.originFileObj.name} file upload failed.`);
                                                        _this.setState({
                                                            uploading: false
                                                        })
                                                    }
                                                }}
                                            >
                                                <Button size='large' style={{width: '100%'}}>
                                                    <Icon type="upload" /> Upload Featured Image
                                                </Button>
                                            </Upload>
                                            <div style={{marginTop: 20, marginBottom: 20}}>
                                                <Row type="flex" justify="space-around" align="middle">

                                                    {
                                                        this.state.thumbimg ?
                                                            _this.generateThumbImages() :
                                                            <span>Select Image to upload</span>
                                                    }
                                                </Row>
                                            </div>
                                        </div>
                                    </Col>
                                    <Col xs={{span: 24}} lg={{span: 8}}>
                                        <div className='col-inner'>

                                            <Select
                                                mode="tags"
                                                className='form-control'
                                                placeholder="Add Tags"
                                                onChange={this.handleChangetags}
                                                value={this.state.selectedtags}
                                            />

                                        </div>
                                    </Col>
                                    <Col xs={{span: 24}} lg={{span: 8}}>
                                        <div className='col-inner'>
                                            <Select
                                                className='form-control'
                                                placeholder="Product Condition"
                                                onChange={(value) => this.handleChangeSelect('product_condition', value)}
                                                value={this.state.product_condition}
                                            >
                                                <Option value=''>Product Condition</Option>
                                                <Option value={1}>New</Option>
                                                <Option value={0}>Old</Option>
                                            </Select>
                                        </div>
                                    </Col>
                                </Row>

                                <Row>
                                    <div className='col-inner'>
                                        <div className='tbpd15'>
                                            <Button
                                                type='primary'
                                                block
                                                size='large'
                                                onClick={() => {
                                                    this.savepost()
                                                }}
                                            >Publish</Button>
                                        </div>

                                    </div>
                                </Row>

                            </Form>

                        </Row>
                    </div>
                </Content>
                <FooterLinks/>
                <BottomFooter/>
            </Layout>

        )
    }

}

const AddPost = Form.create()(AddPostWrapper);

export default AddPost