import React, {Component} from 'react'
import {Row, Col, Layout, Form, Button, Input, message} from 'antd';
import CommonHead from "./CommonHead";
import FooterLinks from "./FooterLinks";
import BottomFooter from "./BottomFooter";
import {postapi} from "../helper";
import UpdateProfile from "./UpdateProfile";
import UpdatePassword from "./UpdatePassword";
import config from "../config";
import {Redirect} from "react-router-dom";
const {Content} = Layout;
const FormItem = Form.Item;

class User extends Component {
    constructor(props) {
        super(props)
        this.state = {
            single: []
        }
    }

    componentDidMount() {
        this.getUser()
    }

    getUser() {
        const _this = this
        const getuser = postapi('getuser', {
            uid: _this.props.match.params.id
        })
        getuser.then((datau) => {
            _this.setState({
                single: datau.data
            })
        })
    }

    render() {
        const {single} = this.state

        const isloggedinuser = config.isLoggedin ? true : false
        if (!isloggedinuser) return <Redirect to='/' />;
        return (
            <Layout>
                <Content>
                    <div className='content'>
                        <CommonHead/>
                        <Row>
                            <Col xs={{span: 24}} lg={{span: 12}}>
                                <UpdateProfile single={this.state.single}/>
                            </Col>
                            <Col xs={{span: 24}} lg={{span: 12}}>
                                <UpdatePassword single={this.state.single}/>
                            </Col>
                        </Row>
                    </div>
                </Content>
                <FooterLinks/>
                <BottomFooter/>
            </Layout>
        )
    }

}

export default User