import React, {Component} from 'react'
import {Row, Col, Button, Table, Divider, Modal, Form, Input, Select, message} from 'antd';
import {Link} from "react-router-dom";
import {callapi, postapi} from "../helper";

const FormItem = Form.Item;
const Option = Select.Option;

class ManagePlacesForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            districts: [],
            cities: [],
            areas: [],

            addcitymodal: false,
            isloadingArea: false,
            area_name: '',
            city_id: '',
            cityload_id: '',

            single: false
        }
    }

    componentDidMount() {
        this.getStates()
        this.getCities()
        this.getArea()
    }


    getStates() {
        const _this = this
        const categories = callapi('getstates')
        categories.then((data) => {
            _this.setState({
                districts: data
            })
        })
    }

    getCities() {
        const _this = this
        const categories = callapi('getcities')
        categories.then((data) => {
            _this.setState({
                cities: data
            })
        })
    }

    getArea(parentid = false) {
        const _this = this

        _this.setState({
            isloadingArea: true
        })

        const parent = {
            parent_id: parseInt(parentid) > 0 ? parseInt(parentid) : 0
        }

        const categories = postapi('getarea', parent)
        categories.then((data) => {
            _this.setState({
                areas: data.data,
                isloadingArea: false
            })
        })
    }

    handleCityChange = (city_id) => {
        this.setState({city_id: city_id});
    }

    handleCityChangetoload = (city_id) => {
        this.setState({cityload_id: city_id});
        this.getArea(city_id)
    }

    addCityModal(addcitymodal) {
        this.setState({addcitymodal});
    }

    handleSubmit = (e) => {
        e.preventDefault();
        const _this = this
        _this.props.form.validateFields((err, values) => {
            if (!err) {

                // const optype = this.state.single ? 'updatecity' : 'addcity'
                //
                // const upcity = this.state.single ? {single_id: this.state.single.city_id} : false

                let addcity = postapi('addcity', values)
                addcity.then((data) => {
                    if (data.status === 'success') {
                        _this.addCityModal(false)
                        message.success('City successfully added.');
                        _this.props.form.resetFields()
                        _this.getArea()
                    }
                    else {
                        message.error(`${data.msg}.`);
                    }
                })
            }
        });
    }

    deleteCity(id){
        const _this = this
        const deletecity = postapi('deletecity', {
            city_id: id
        })
        deletecity.then((data) => {
            if (data.status === 'success') {
                message.success('City successfully deleted.');
                _this.getArea()
            }
            else {
                message.error(`${data.msg}.`);
            }
        })
    }

    render() {
        const {getFieldDecorator} = this.props.form;
        const columnsarea = [
            {
                title: 'Area',
                dataIndex: 'city_name',
                key: 'city_name',
            },
            {
                title: 'Actions',
                dataIndex: 'actions',
                key: 'actions',
                render: (text, record) => {
                    return (
                        <div>
                            {/*<Button icon='edit' onClick={() =>{*/}
                                {/*this.setState({*/}
                                    {/*single: record,*/}
                                    {/*city_id: record.parent_id,*/}
                                    {/*area_name: record.city_name*/}
                                {/*}, () => {*/}
                                    {/*this.addCityModal(true)*/}
                                {/*})*/}

                            {/*}}/>*/}
                            {/*<Divider type='vertical'/>*/}
                            <Button icon='delete' type='danger'
                                    onClick={() => this.deleteCity(record.city_id)}
                            />
                        </div>
                    )
                }
            },
        ];

        return (
            <div>
                <div className='helper-bar'>
                    <Button type='primary'
                            onClick={() => this.addCityModal(true)}
                    >Add City</Button>
                </div>

                <Row>
                    <Col xs={{span: 24}} lg={{span: 8}}>
                        <div className='col-inner'>
                            <Select
                                style={{width: '100%'}}
                                defaultValue={this.state.cityload_id}
                                placeholder='Select City'
                                onChange={this.handleCityChangetoload}
                            >
                                <Option key='blank' value={0}>Select City</Option>
                                {
                                    this.state.cities.map((item, index) => {
                                        return <Option key={index} value={item.city_id}>{item.city_name}</Option>
                                    })
                                }
                            </Select>
                        </div>
                    </Col>
                    <Col xs={{span: 24}} lg={{span: 8}}></Col>
                    <Col xs={{span: 24}} lg={{span: 8}}></Col>
                </Row>

                <Row>
                    <div className='col-inner'>
                        <Table loading={this.state.isloadingArea} size='small' columns={columnsarea} dataSource={this.state.areas}/>
                    </div>
                </Row>
                <Modal
                    title={`${this.state.single ? 'Update' : 'Add'} City`}
                    centered
                    footer={null}
                    visible={this.state.addcitymodal}
                    onOk={() => this.addCityModal(false)}
                    onCancel={() => this.addCityModal(false)}
                >
                    <Form onSubmit={this.handleSubmit}>
                        <FormItem
                            label='City Name'
                        >
                            {getFieldDecorator('city_id', {
                                initialValue: this.state.city_id,
                                rules: [{required: true, message: 'Please type city name!'}],
                            })(
                                <Select
                                    onChange={this.handleCityChange}
                                >
                                    {
                                        this.state.cities.map((item, index) => {
                                            return <Option key={index} value={item.city_id}>{item.city_name}</Option>
                                        })
                                    }
                                </Select>
                            )}
                        </FormItem>

                        <FormItem
                            label='Area Name'
                        >
                            {getFieldDecorator('area_name', {
                                initialValue: this.state.area_name,
                                rules: [{required: true, message: 'Please type area name!'}],
                            })(
                                <Input placeholder="Area Name"/>
                            )}
                        </FormItem>

                        <FormItem>

                            <Button type="primary" block htmlType='submit'>
                                {`${this.state.single ? 'Update' : 'Save'} City`}
                            </Button>
                        </FormItem>
                    </Form>
                </Modal>
            </div>
        )
    }

}

const ManagePlaces = Form.create()(ManagePlacesForm);

export default ManagePlaces