import React, {Component} from 'react'
import config from "../config";
import {Helmet} from "react-helmet";
import MainMenu from './MainMenu'
import DrawerMain from './DrawerMain'

class CommonHead extends Component {
    constructor(props) {
        super(props)

        this.state = {
            drawerOpen: false,
            loginModal: false,
            current: ''
        }
        this.toggleDrawer = this.toggleDrawer.bind(this)
    }

    toggleDrawer() {
        this.setState({
            drawerOpen: !this.state.drawerOpen,
        });
    };

    formaturl = (url) => {
        return url.replace(/\/$/, "");
    }

    toggleLoginModal = () => {
        this.setState({
            loginModal: !this.state.loginModal,
        });
    }

    render() {

        const {
            title,
            description
        } = this.props

        const currenturl = this.formaturl(window.location.href)

        const routes = [
            {
                path: "/",
                exact: true,
                menu: 'Home',
                key: 'home',
            },
            {
                path: "/ads/all",
                exact: true,
                menu: 'All Ads',
                key: 'allads',
            },
        ];

        return (
            <div>
                <Helmet>
                    <title>{title ? title : config.siteName}</title>
                    <meta name="description" content={description ? description : config.description}/>
                    <meta name="twitter:card" content="summary"/>
                    <meta name="twitter:site" content={`@${config.siteName}`}/>
                    <meta name="og:title" content={title}/>
                    <meta name="og:description" content={description ? description : config.description}/>

                    <meta property="og:image" content={config.sitelogo}/>
                    <meta name="description" content={description ? description : config.description}/>
                    <meta property="og:description" content={description ? description : config.description}/>
                    <meta property="og:title" content={title ? title : config.siteName}/>
                    <meta property="og:url" content={currenturl}/>
                    <meta property="og:site_name" content={config.siteName}/>
                    <meta name="keywords" content={config.keywords}/>
                    <meta name="twitter:card" content="summary"/>
                    <meta name="twitter:url" content={currenturl}/>
                    <meta name="twitter:title" content={title ? title : config.siteName}/>
                    <meta name="twitter:description" content={description ? description : config.description}/>

                </Helmet>
                <MainMenu
                    current={this.state.current}
                    toggleDrawer={this.toggleDrawer}
                    routes={routes}
                />
                <DrawerMain
                    toggleDrawer={this.toggleDrawer}
                    drawerOpen={this.state.drawerOpen}
                    current={this.state.current}
                    routes={routes}
                />
            </div>

        )
    }

}

export default CommonHead