import React, {Component} from 'react'
import {Row, Col,} from 'antd';


class Dashboard extends Component {
    constructor(props) {
        super(props)

        this.state = {
            paletteHeight: window.innerHeight - 100,
        }
    }


    render() {
        return (
            <div style={{height: this.state.paletteHeight}}>
                Dashboard
            </div>
        )
    }

}

export default Dashboard