import React, {Component} from 'react'
import {Row, Col, Form, Button, Input, message, Icon} from 'antd';
import {postapi} from "../helper";

const FormItem = Form.Item;

class UpdatePasswordForm extends Component {
    constructor(props) {
        super(props)
        this.state = {
            confirmDirty: false
        }
    }

    handleSubmit = (e) => {
        const _this = this

        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                _this.setState({
                    loading: true
                })

                const doregister = postapi('updatepassword', {
                    id: _this.props.single.uid,
                    password: values.confirmPassword,
                })

                doregister.then((data) => {
                    if (data.status === 'error') {
                        message.error(data.msg)
                        _this.setState({
                            loading: false
                        })
                    }
                    else {
                        _this.props.form.resetFields()
                        message.success('Your password successfully updated.')
                    }
                })
            }
        });
    }

    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback("Passwords doesn't match!");
        } else {
            callback();
        }
    }

    validateToNextPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], { force: true });
        }
        callback();
    }

    render() {
        const {single} = this.props

        const { getFieldDecorator } = this.props.form;

        const formItemLayout = {
            labelCol: {
                xs: { span: 24 },
                sm: { span: 8 },
            },
            wrapperCol: {
                xs: { span: 24 },
                sm: { span: 16 },
            },
        };

        return (
            <div className='col-inner'>
                <h3>Update Password</h3>
                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                        {...formItemLayout}
                        label="Password">
                        {getFieldDecorator('password', {
                            rules: [
                                {required: true, message: 'Please input your Password!'},
                                {validator: this.validateToNextPassword},
                            ],
                        })(
                            <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
                                   placeholder="Password"/>
                        )}
                    </FormItem>

                    <FormItem
                        {...formItemLayout}
                        label="Confirm Password">
                        {
                            getFieldDecorator('confirmPassword', {
                                rules: [{required: true, message: 'Confirm password and password must match'},
                                    {validator: this.compareToFirstPassword}],
                            })
                            (
                                <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                       type="password" placeholder="Confirm Password"/>
                            )
                        }
                    </FormItem>

                    <FormItem style={{textAlign: 'right'}}>
                        <Button type='primary' htmlType='submit'>Update Password</Button>
                    </FormItem>

                </Form>
            </div>
        )
    }

}


const UpdatePassword = Form.create()(UpdatePasswordForm);

export default UpdatePassword