import React, {Component} from 'react'
import {Button, Drawer, Menu, Modal, Tabs} from 'antd';
import { Link } from 'react-router-dom'
import config from "../config";
import {reactLocalStorage} from "reactjs-localstorage";
import SignIn from "./SigninForm";
import SignUp from "./SignupForm";
const TabPane = Tabs.TabPane;

class DrawerMain extends Component {
    constructor(props) {
        super(props)
        this.state = {
            openLoginModal: false,
        }
    }

    toggleLoginModal = (openLoginModal) => {
        this.setState({
            openLoginModal: openLoginModal,
        });
    }

    onClose = () => {
        this.props.toggleDrawer()
    };

    render() {
        return (
            <div>
                <Drawer
                    title={config.siteName}
                    placement="left"
                    closable={false}
                    onClose={this.onClose}
                    visible={this.props.drawerOpen}
                >
                    <Menu
                        style={{width: 232}}
                        onClick={this.props.toggleDrawer}
                        selectedKeys={[this.props.current]}
                        mode="vertical"
                    >
                        {
                            this.props.routes.map((route, index) => (
                                <Menu.Item key={route.key}>
                                    <Link to={route.path}>{route.menu}</Link>
                                </Menu.Item>
                            ))
                        }

                        {
                            (config.isLoggedin && config.user.user_type === 'public') &&
                            <Menu.Item key='/posts/addnew'>
                                <Link to='/posts/addnew'>Add Post</Link>
                            </Menu.Item>
                        }

                        {
                            (config.isLoggedin && config.user.user_type === 'public') &&
                            <Menu.Item key="user:1">
                                <Link to={`/user/${config.user.uid}`}>Profile</Link>
                            </Menu.Item>
                        }

                        {
                            (config.isLoggedin && config.user.user_type === 'public') &&
                            <Menu.Item key="user:2">
                                <Link to='/posts/my-posts'>My Posts</Link>
                            </Menu.Item>
                        }

                        {
                            (config.isLoggedin && config.user.user_type === 'public') &&
                            <Menu.Item key="user:3" onClick={() => {
                                reactLocalStorage.clear('user')
                                reactLocalStorage.clear('isLoggedin')
                                window.location = '/'
                            }}><a>Logout</a></Menu.Item>
                        }

                        {
                            (!config.isLoggedin) &&
                            <Menu.Item key='/login'>
                                <a onClick={() => {
                                    this.toggleLoginModal(true)
                                }}>Login</a>
                            </Menu.Item>

                        }

                    </Menu>
                </Drawer>
                <Modal
                    visible={this.state.openLoginModal}
                    footer={null}
                    onOk={() => this.toggleLoginModal(false)}
                    onCancel={() => this.toggleLoginModal(false)}
                >
                    <Tabs defaultActiveKey="1">
                        <TabPane tab="Login" key="1">
                            <SignIn {...this.props} SignInModal={() => this.toggleLoginModal(false)}/>
                        </TabPane>
                        <TabPane tab="Register" key="2">
                            <SignUp {...this.props} SignInModal={() => this.toggleLoginModal(false)}/>
                        </TabPane>
                    </Tabs>
                </Modal>
            </div>
        )
    }

}

export default DrawerMain