import React, {Component} from 'react';
import { Layout, Menu, Button, Modal, Tabs, Icon } from 'antd';
import {reactLocalStorage} from "reactjs-localstorage";
import { Link } from 'react-router-dom'
import config from "../config";
import SignIn from './SigninForm'
import SignUp from './SignupForm'
const { Header } = Layout;
const TabPane = Tabs.TabPane;
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;


function callback(key) {
    console.log(key);
}

class MainMenu extends Component {

    constructor(props) {
        super(props)
        this.state = {
            current: this.props.current,
            addShadow: false,
            openLoginModal: false,
        }
    }

    toggleLoginModal = (openLoginModal) => {
        this.setState({
            openLoginModal: openLoginModal,
        });
    }

    handleClick = (e) => {
        if (e.key === this.state.current){
            return;
        }
        this.setState({
            current: e.key,
        });
    }

    logout() {
        reactLocalStorage.clear('user')
        reactLocalStorage.clear('isLoggedin')
        window.location = '/'
    }

    render () {
        return (
            <Header className='front-header'>
                <div className='header-content'>
                    <div>
                        <img src={config.sitelogo} className='header-logo'/>
                    </div>

                    <Menu
                        onClick={this.handleClick}
                        selectedKeys={[this.state.current]}
                        mode="horizontal"
                        className='desktopmenu'
                    >

                        {
                            this.props.routes.map((route, index) => (
                                <Menu.Item key={route.key}>
                                    <Link to={route.path}>{route.menu}</Link>
                                </Menu.Item>
                            ))
                        }
                        <Menu.Item key='postad'>
                            {
                                (config.isLoggedin && config.user.user_type === 'public') ?
                                    <Link to='/posts/addnew' className='ant-btn ant-btn-primary csbtn'>
                                        <span>Post Ad</span>
                                    </Link> :
                                    <Button type='primary'
                                            onClick={() => this.toggleLoginModal(true)}
                                    >Post Ad</Button>
                            }
                        </Menu.Item>
                        {
                            (config.isLoggedin && config.user.user_type === 'public') ?
                                <SubMenu title={<Icon type="user" />}>
                                    <Menu.Item key="user:1">
                                        <Link to={`/user/${config.user.uid}`}>Profile</Link>
                                    </Menu.Item>
                                    <Menu.Item key="user:2">
                                        <Link to='/posts/my-posts'>My Posts</Link>
                                    </Menu.Item>
                                    <Menu.Item key="user:3" onClick={() => this.logout()}>Logout</Menu.Item>
                                </SubMenu> :
                                <Menu.Item key='login'>
                                    <Button type='primary' ghost
                                            onClick={() => this.toggleLoginModal(true)}
                                    >Login</Button>
                                </Menu.Item>
                        }

                    </Menu>
                    <Menu
                        onClick={this.handleClick}
                        selectedKeys={[this.state.current]}
                        mode="horizontal"
                        className='mobilemenu'
                    >

                        <Menu.Item key='toggle'>
                            <Button onClick={() => {this.props.toggleDrawer()}} icon='bars'/>
                        </Menu.Item>

                    </Menu>
                </div>
                <Modal
                    visible={this.state.openLoginModal}
                    footer={null}
                    onOk={() => this.toggleLoginModal(false)}
                    onCancel={() => this.toggleLoginModal(false)}
                >
                    <Tabs defaultActiveKey="1" onChange={callback}>
                        <TabPane tab="Login" key="1">
                            <SignIn {...this.props} SignInModal={() => this.toggleLoginModal(false)}/>
                        </TabPane>
                        <TabPane tab="Register" key="2">
                            <SignUp {...this.props} SignInModal={() => this.toggleLoginModal(false)}/>
                        </TabPane>
                    </Tabs>
                </Modal>
            </Header>

        )
    }
}

export default MainMenu;
