import React, {Component} from 'react'
import {Row, Col, Input, Icon, Cascader} from 'antd';
import {Link, Redirect} from "react-router-dom";

const Search = Input.Search;

class HomeTop extends Component {
    constructor(props) {
        super(props)

        this.state = {
            selectedPlace: ''
        }
        this.onChangeplace = this.onChangeplace.bind(this)
    }

    onChangeplace = (value, selectedOptions) => {
        this.setState({
            selectedPlace: selectedOptions.map(o => o.label).join(' / '),
        });
    }

    onSearch(value) {
        window.location = '/ads/all/?s='+value
    }


    render() {

        const options = [{
            value: 'zhejiang',
            label: 'Zhejiang',
            children: [{
                value: 'hangzhou',
                label: 'Hangzhou',
                children: [{
                    value: 'xihu',
                    label: 'West Lake',
                }],
            }],
        }, {
            value: 'jiangsu',
            label: 'Jiangsu',
            children: [{
                value: 'nanjing',
                label: 'Nanjing',
                children: [{
                    value: 'zhonghuamen',
                    label: 'Zhong Hua Men',
                }],
            }],
        }];

        return (
            <div className='col-inner'>
                <Row>
                    <Col xs={{span: 24}} lg={{span: 4}}></Col>
                    <Col xs={{span: 24}} lg={{span: 16}}>

                        <div className='csinput mb30'>
                            <Search
                                placeholder="Search . . ."
                                enterButton="All Categories"
                                size="large"
                                onSearch={value => this.onSearch(value)}
                            />
                        </div>
                    </Col>
                    <Col xs={{span: 24}} lg={{span: 4}}></Col>
                </Row>
                <div>
                    <b className="deep-green">আজ হাট ডট কম</b> নতুন ও পুরানো জিনিসপত্রের বাজার। আপনি বিনামূল্যে
                    যতখুশি <b className="deep-green">লাইভ অ্যাড পোস্ট</b> করুন। দোকান, শো-রুম ব্যবসার প্রসারে <b
                    className="deep-green"> ফ্রি লোকাল স্টোর পেজ</b> খুলুন। আপনার <b className="deep-green"> ব্রান্ড
                    শোকেসিং</b> করুন। প্রতিযোগিতামূলক দেশীয় মার্কেটপ্লেস তৈরীতে পাশে থাকুন। ইচ্ছেমত বেচুন। পছন্দমত
                    কিনুন। আজ হাট ডট কম … <b className="deep-green">বেচাকেনায় গাল ভরা হাসি।</b>
                </div>
            </div>
        )
    }

}

export default HomeTop