import React, {Component} from 'react'
import {Row, Col, Button, Table, Divider, Icon, Layout, message} from 'antd';
import {Link, Redirect} from 'react-router-dom'
import {postapi} from "../helper";
import config from "../config";
import CommonHead from "./CommonHead";
import FooterLinks from "./FooterLinks";
import BottomFooter from "./BottomFooter";
const {Content} = Layout;


class MyPosts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            products: [],
            loadingproducts: false
        }
    }

    componentDidMount() {
        this.getProducts()
    }

    getProducts() {
        const _this = this
        _this.setState({
            loadingproducts: true
        })
        const getproducts = postapi('getproductsbyuser', {
            uid: config.user.uid
        })
        getproducts.then((data) => {
            if (data) {
                _this.setState({
                    products: data.data,
                    loadingproducts: false
                })
            }
            else {
                _this.setState({
                    loadingproducts: false
                })
            }

        })

    }

    deleteTrack(id){
        const _this = this
        const deletetrack = postapi('deleteproduct', {
            pid: id
        })

        deletetrack.then((data) => {
            if (data.status === 'error') {
                message.error(data.msg)
            }
            else {
                message.success(data.msg)
                this.getProducts()
            }
        })
    }

    render() {
        const isloggedinuser = config.isLoggedin ? true : false
        if (!isloggedinuser) return <Redirect to='/' />;
        const columns = [
            {
                title: 'Title',
                dataIndex: 'p_title',
                key: 'p_title',
            },
            {
                title: 'Price',
                dataIndex: 'product_price',
                key: 'product_price',
            },
            {
                title: 'Address',
                dataIndex: 'product_address',
                key: 'product_address',
            },
            {
                title: 'Actions',
                dataIndex: 'actions',
                key: 'actions',
                render: (text, record) => {
                    return (
                        <div>
                            <Link className='ant-btn ant-btn-icon-only'
                                  to={{
                                      pathname: "/posts/editpost",
                                      search: "?id="+record.key,
                                  }}><Icon type='edit'/></Link>
                            <Divider type="vertical" />
                            <Button type='danger' icon='delete' onClick={() => this.deleteTrack(record.pid)}/>
                        </div>
                    )
                }
            },
        ];

        return (
            <div>
                <Layout>
                    <Content>
                        <div className='content'>
                            <CommonHead/>
                            <h3 style={{textAlign: 'center'}}>My Products</h3>
                            <Row>
                                <Col xs={{span: 24}} lg={{span: 6}}>
                                    Help
                                </Col>
                                <Col xs={{span: 24}} lg={{span: 18}}>
                                    <div className='helper-bar'>
                                        <Link to='/posts/addnew' className='ant-btn ant-btn-primary'>Add New Product</Link>
                                    </div>

                                    <Table size='small' loading={this.state.loadingproducts} columns={columns} dataSource={this.state.products} />

                                </Col>
                            </Row>
                        </div>
                    </Content>
                    <FooterLinks/>
                    <BottomFooter/>
                </Layout>

            </div>
        )
    }

}

export default MyPosts