import React, {Component} from 'react'
import { Upload, Button, Icon, message } from 'antd';
import config from "../config";

class Uploader extends Component {
    constructor(props) {
        super(props)

        this.state = {
            fileList: [],
            uploading: false,
            thumbimg: ''
        }

    }

    componentDidMount(){
        console.log(this.props.defaultImg)
    }

    render() {
        const _this = this
        return (
            <div style={{marginTop: 20, marginBottom: 20}}>
                <Upload
                    accept="image/*"
                    name={this.props.name}
                    action={`${config.apiurl}uploadimage`}
                    data={{
                        folder: this.props.uploadFolder
                    }}
                    showUploadList={false}
                    defaultFileList={[]}

                    onChange={(info) => {
                        _this.setState({
                            uploading: true
                        })
                        const status = info.file.status;

                        if (status === 'done') {
                            const resp = info.file.response.results;
                            if (resp.status === 'success') {
                                message.success(`${info.file.originFileObj.name} file uploaded successfully.`);
                                _this.setState({
                                    uploading: false,
                                    thumbimg: config.fileurl+resp.data
                                })
                                this.props.onSuccess(info.file)
                            }
                            else {
                                message.error(`${info.file.originFileObj.name} - ${resp.msg}.`);
                                _this.setState({
                                    uploading: false
                                })
                            }
                        } else if (status === 'error') {
                            message.error(`${info.file.originFileObj.name} file upload failed.`);
                            _this.setState({
                                uploading: false
                            })
                        }
                    }}
                >
                    <Button size='large' style={{width: '100%'}}>
                        <Icon type="upload" /> Upload Featured Image
                    </Button>
                </Upload>
                <div className='imgPreviewer'>

                    {
                        this.state.thumbimg ?
                            <img style={{width: '30%'}} src={this.state.thumbimg}/> :
                            <span>Select Image to upload</span>
                    }
                </div>
            </div>
        )
    }

}

export default Uploader