import React, {Component} from 'react';
import {Form, Icon, Input, Button, message} from 'antd';
import {reactLocalStorage} from "reactjs-localstorage";
import {postapi} from "../helper";

const FormItem = Form.Item;

class SignupForm extends Component {

    constructor(props) {
        super(props)
        this.state = {
            loading: false,
            confirmDirty: false
        }
    }

    login(){
        const _this = this
        console.log('please setup fb cred')

    }

    handleSubmit = (e) => {
        const _this = this

        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                _this.setState({
                    loading: true
                })

                const doregister = postapi('register', {
                    email: values.email,
                    password: values.password,
                    fullname: values.fullname,
                    phone: values.phone,
                })

                doregister.then((data) => {
                    if (data.status === 'error') {
                        message.error(data.msg)
                        _this.setState({
                            loading: false
                        })
                    }
                    else {
                        const getuser = postapi('getuser', {
                            uid: data.data
                        })
                        getuser.then((datau) => {
                            message.success('You have successfully registered.', () => {
                                reactLocalStorage.setObject('user', datau.data)
                                reactLocalStorage.set('islogged', true)
                                _this.setState({
                                    loading: false,
                                }, () => {
                                    _this.props.SignInModal()
                                    window.location.reload()
                                })
                            })

                        })

                    }
                })
            }
        });
    }

    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback("Passwords doesn't match!");
        } else {
            callback();
        }
    }

    validateToNextPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], { force: true });
        }
        callback();
    }

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <div>
                <div className='sociallogin-container'>
                    <Button
                        onClick={() => {
                            this.login()
                        }}
                        size='large'
                        icon="facebook"
                        className='fbbtn'>
                        Continue with Facebook
                    </Button>
                    <h4>Or</h4>
                </div>

                <Form onSubmit={this.handleSubmit} className="login-form">
                    <FormItem>
                        {getFieldDecorator('email', {
                            rules: [{required: true, message: 'Please input your email!'}],
                        })(
                            <Input prefix={<Icon type="mail" style={{color: 'rgba(0,0,0,.25)'}}/>} placeholder="Email"/>
                        )}
                    </FormItem>
                    <FormItem>
                        {getFieldDecorator('password', {
                            rules: [
                                {required: true, message: 'Please input your Password!'},
                                {validator: this.validateToNextPassword},
                            ],
                        })(
                            <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
                                   placeholder="Password"/>
                        )}
                    </FormItem>

                    <FormItem>
                        {
                            getFieldDecorator('confirmPassword', {
                                rules: [{required: true, message: 'Confirm password and password must match'},
                                    {validator: this.compareToFirstPassword}],
                            })
                            (
                                <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                       type="password" placeholder="Confirm Password"/>
                            )
                        }
                    </FormItem>

                    <FormItem>
                        {getFieldDecorator('fullname', {
                            rules: [{required: true, message: 'Please type full name!'}],
                        })(
                            <Input placeholder="Full Name"/>
                        )}
                    </FormItem>

                    <FormItem>
                        {getFieldDecorator('phone', {
                            rules: [{required: true, message: 'Please type phone!'}],
                        })(
                            <Input placeholder="Phone"/>
                        )}
                    </FormItem>

                    <FormItem>
                        <Button
                            loading={this.state.loading}
                            type="primary"
                            htmlType="submit"
                            className="login-form-button">
                            Sign Up
                        </Button>
                    </FormItem>
                </Form>
            </div>
        );

    }
}

const SignUp = Form.create()(SignupForm);

export default SignUp