import React, {Component} from 'react'
import {Layout, Icon, Button, Menu, Drawer} from 'antd';

import {
    HashRouter as Router,
    Route,
    Link, Switch
} from "react-router-dom";
import Dashboard from "./Dashboard";
import {reactLocalStorage} from 'reactjs-localstorage'
import AddPost from "./AddPost";
import EditPost from "./EditPost";
import Categories from "./Categories";
import ManagePosts from "./ManagePosts";
import CategoriesMeta from "./CategoriesMeta";

import config from "../config";
import ManagePlaces from "./ManagePlaces";

const {Header, Footer, Sider, Content} = Layout;

class Admin extends Component {
    constructor(props) {
        super(props)

        this.state = {
            sidebarVisible: false,
            current: 1,
        }
    }

    showDrawer = () => {
        this.setState({
            sidebarVisible: true,
        });
    };

    onClose = () => {
        this.setState({
            sidebarVisible: false,
        });
    };


    handleSidebarClick = (e) => {
        this.setState({
            current: e.key,
        });
        this.onClose()
    }

    logout() {

        //console.log('cl')
        reactLocalStorage.clear('user')
        window.location.reload()
    }

    render() {

        const routes = [
            {
                path: '/',
                exact: true,
                menuName: 'Dashboard',
                menuIcon: <Icon type="appstore"/>,
                sidebar: () => <h4>Dashboard</h4>,
                content: () => <Dashboard/>,
            },
            {
                path: "/addpost",
                menuName: 'Add Post',
                menuIcon: <Icon type="appstore"/>,
                sidebar: () => <h4>Add Post</h4>,
                content: () => <AddPost {...config}/>
            },
            {
                path: "/editpost",
                menuName: 'Edit Post',
                menuIcon: <Icon type="appstore"/>,
                sidebar: () => <h4>Edit Post</h4>,
                content: () => <EditPost {...config}/>
            },
            {
                path: "/manageposts",
                menuName: 'All Posts',
                menuIcon: <Icon type="appstore"/>,
                sidebar: () => <h4>All Posts</h4>,
                content: () => <ManagePosts {...config}/>
            },
            {
                path: "/categories",
                menuName: 'Manage Categories',
                menuIcon: <Icon type="appstore"/>,
                sidebar: () => <h4>Manage Categories</h4>,
                content: () => <Categories {...config}/>
            },
            {
                path: "/categoriesmeta",
                menuName: 'Manage Category meta',
                menuIcon: <Icon type="appstore"/>,
                sidebar: () => <h4>Dashboard</h4>,
                content: () => <CategoriesMeta {...config}/>
            },
            {
                path: "/ManagePlaces",
                menuName: 'Manage Places',
                menuIcon: <Icon type="heat-map"/>,
                sidebar: () => <h4>Manage Places</h4>,
                content: () => <ManagePlaces {...config}/>
            },
        ]

        return (
            <Router>
                <Layout>
                    <Sider
                        breakpoint="md"
                        collapsedWidth="0"
                        className='sidebar-admin'>
                        <div className='sidebarheader'>
                            <img src={config.sitelogo}/>
                        </div>
                        <div className='user-profileinfo'>
                            <h4>{config.user.fullname}</h4>
                            <h4>{config.user.email}</h4>
                            <h4>{config.user.usertype}</h4>
                        </div>
                        <div className='sidebarmenu-wrapper'>
                            <Menu
                                mode="inline"
                                style={{width: 256}}
                            >
                                {routes.map((route, index) => {
                                    if (route.path !== '/addpost' && route.path !== '/categoriesmeta' && route.path !== '/editpost') {
                                        return (
                                            <Menu.Item key={index}>
                                                <Link to={route.path}>
                                                    {route.menuIcon}
                                                    <span className="nav-text">{route.menuName}</span>
                                                </Link>
                                            </Menu.Item>
                                        )
                                    }
                                })}
                            </Menu>
                        </div>
                    </Sider>
                    <Drawer
                        title={config.siteName}
                        placement='left'
                        closable={false}
                        onClose={this.onClose}
                        visible={this.state.sidebarVisible}
                    >
                        <div className='user-profileinfo'>
                            <h4>{config.user.firstName + ' ' + config.user.lastName}</h4>
                            <h4>{config.user.email}</h4>
                            <h4>{config.user.usertype}</h4>
                        </div>
                        <div className='sidebarmenu-wrapper sidebarmenu-wrapper-fix'>
                            <Menu
                                onClick={this.handleSidebarClick}
                                mode="inline"
                                openKeys={this.state.openKeys}
                                onOpenChange={this.onOpenChange}
                                style={{width: 256}}
                            >
                                {
                                    routes.map((route, index) => {
                                        if (route.path !== '/addpost' && route.path !== '/categoriesmeta') {
                                            return (
                                                <Menu.Item key={index}>
                                                    <Link to={route.path}>
                                                        {route.menuIcon}
                                                        <span className="nav-text">{route.menuName}</span>
                                                    </Link>
                                                </Menu.Item>
                                            )
                                        }
                                    })
                                }
                            </Menu>
                        </div>
                    </Drawer>
                    <Layout className='hasSidebar'>
                        <Header className='header-admin'>

                            <div className='pagetitle'>
                                {
                                    routes.map((route, index) => (<Route
                                        key={index}
                                        path={route.path}
                                        exact={route.exact}
                                        component={route.sidebar}
                                    />))
                                }
                            </div>
                            <Button
                                className='mobiletoggle'
                                icon={this.state.sidebarVisible ? 'arrow-left' : 'arrow-right'}
                                onClick={() => {
                                    this.showDrawer()
                                }}
                            />
                            <div style={{
                                float: 'right'
                            }}>
                                <Button type="primary" shape="circle" icon="logout"
                                        onClick={() => {
                                            this.logout()
                                        }}
                                />
                            </div>
                        </Header>
                        <Content style={{padding: 20}}>
                            {
                                routes.map((route, index) => (
                                    <Route
                                        key={index}
                                        path={route.path}
                                        exact={route.exact}
                                        component={route.content}
                                    />
                                ))
                            }
                        </Content>
                        <Footer style={{
                            display: 'flex',
                            justifyContent: 'center'
                        }}><span>{config.copyright}. </span>
                        </Footer>
                    </Layout>
                </Layout>
            </Router>
        )
    }

}

export default Admin