import React, {Component} from 'react'
import {Form, Icon, Input, Button, Checkbox, message} from 'antd';
import { Redirect } from "react-router-dom";
import {reactLocalStorage} from "reactjs-localstorage";
import {postapi} from "../helper";

const FormItem = Form.Item;

class SigninForm extends Component {
    
    constructor(props, context) {
        super(props, context)
        this.state = {
            loading: false
        }

    }

    handleSubmit = (e) => {
        e.preventDefault();
        const _this = this

        this.props.form.validateFields((err, values) => {
            if (!err) {
                _this.setState({
                    loading: true,
                })
                const dologin = postapi('userlogin', {
                    email: values.userName,
                    password: values.password
                })

                dologin.then((data) => {
                    if (data.status === 'error') {
                        message.error(data.msg)
                        _this.setState({
                            loading: false
                        })
                    }
                    else {
                        reactLocalStorage.setObject('user', data.data)
                        reactLocalStorage.set('islogged', true)
                        _this.setState({
                            loading: false,
                        }, () => {
                            window.location.reload()
                        })
                    }
                })
            }
        });
    }

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <div>
                <div className='sociallogin-container'>
                    <Button
                        onClick={() => {
                            console.log('please setup fb cred')
                        }}
                        size='large'
                        icon="facebook"
                        className='fbbtn'>
                        Continue with Facebook
                    </Button>
                    <h4>Or</h4>
                </div>
                <Form onSubmit={this.handleSubmit} className="login-form">
                    <FormItem>
                        {getFieldDecorator('userName', {
                            rules: [{required: true, message: 'Please input your username!'}],
                        })(
                            <Input prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>}
                                   placeholder="Username"/>
                        )}
                    </FormItem>
                    <FormItem>
                        {getFieldDecorator('password', {
                            rules: [{required: true, message: 'Please input your Password!'}],
                        })(
                            <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
                                   placeholder="Password"/>
                        )}
                    </FormItem>
                    <FormItem>
                        {getFieldDecorator('remember', {
                            valuePropName: 'checked',
                            initialValue: true,
                        })(
                            <Checkbox className='dispBlock'>Remember me</Checkbox>
                        )}
                        <a className="login-form-forgot dispBlock" href="">Forgot password</a>
                        <Button loading={this.state.loading} type="primary" htmlType="submit" className="login-form-button">
                            Log in
                        </Button>

                    </FormItem>
                </Form>
            </div>
        );
    }

}

const SignIn = Form.create()(SigninForm);

export default SignIn