import React, {Component} from 'react'
import {Row, Col,} from 'antd';
import {Link} from "react-router-dom";
import {callapi, postapi} from "../helper";
import config from '../config'


class HomeCategories extends Component {

    constructor(props) {
        super(props)
        this.state = {
            categories: [],
        }
    }

    componentWillMount() {
        this.getCategories()
    }

    getCategories() {
        const _this = this
        const categories = postapi('getcategories', {
            ignore_special: true
        })
        categories.then((data) => {
            _this.setState({
                categories: data.data
            })
        })
    }

    render() {
        return (
            <div>
                <div className='section-title'>
                    <h2>সকল শ্রেণী</h2>
                </div>
                <Row type="flex" justify="center">
                    {
                        this.state.categories.map((item, index) => {
                            return (
                                <Col key={index} xs={{span: 24}} lg={{span: 6}}>
                                    <div className='catslink'>
                                        <Link to={`/ads/${item.cat_slug}`}>
                                            <img src={config.fileurl+item.cat_icon}/>
                                            <span>{item.cat_name_en}</span>
                                        </Link>
                                    </div>
                                </Col>
                            )
                        })
                    }

                </Row>
            </div>
        )
    }

}

export default HomeCategories