import React from "react";
import {
    BrowserRouter as Router,
    Route,
    Redirect, Switch
} from "react-router-dom";
import config from "./config";
import Home from './frontend'
import SpecialCategories from './frontend/SpecialCategories'

import Admin from './admin'
import Login from './admin/Login'
import AllAds from "./frontend/AllAds";
import Details from "./frontend/Details";
import User from "./frontend/User";
import MyPosts from "./frontend/MyPosts";
import AddPost from "./frontend/AddPost";
import EditPost from "./frontend/EditPost";

function App() {

    const isauthted = (config.isLoggedin && config.user.user_type === 'admin') ? true : false

    return (
        <Router>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/login" component={Login} />
                <Route path='/user/:id' component={User}/>
                <Route path='/posts/my-posts' component={MyPosts}/>
                <Route path='/posts/addnew' component={AddPost}/>
                <Route path='/posts/editpost' component={EditPost}/>

                <Route path='/special/:scat' component={SpecialCategories}/>

                <Route exact path='/ads/:category' render={(props) => {
                    return <AllAds {...props}/>
                }}/>

                <Route path='/product/:id' component={Details}/>
                <PrivateRoute authed={isauthted} path='/admin' component={Admin} />
            </Switch>
        </Router>
    );
}

function PrivateRoute ({component: Component, authed, ...rest}) {
    return (
        <Route
            {...rest}
            render={(props) => authed === true
                ? <Component {...props} />
                : <Redirect to={{pathname: '/login', state: {from: props.location}}} />}
        />
    )
}


export default App;