import React, {Component} from 'react'
import {Link} from "react-router-dom";
import {callapi} from "../helper";
import config from "../config";

class HomeSpecial extends Component {
    constructor(props) {
        super(props)
        this.state = {
            specialcategories: [],
        }
    }

    componentDidMount() {
        this.getSpecialCategories()
    }


    getSpecialCategories() {
        const _this = this
        const categories = callapi('getspecialcat')
        categories.then((data) => {
            _this.setState({
                specialcategories: data
            })
        })
    }

    render() {
        return (
            <div className='homespecial-icon'>
                <ul>
                    {
                        this.state.specialcategories.map((item, index) => {
                            return (
                                <li key={index}>
                                    <Link to={`/special/${item.cat_slug}`}>
                                        <img className='img-responsive' src={config.fileurl+item.cat_icon}/>
                                        <h4>{item.cat_name_en}</h4>
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        )
    }

}

export default HomeSpecial